import axios from 'axios';

export default {
    namespaced: true,
    state: () => ({
        user: {
            userName: '',
            firstName: '',
            lastName: '',
            middleName: '',
            title: '',
            email: '',
            cellPhone: '',
            officeLocation: '',
            gender: '',
            birthData: '',
            internalPhone: '',
            photo: ''
        },
        userData: {}
    }),
    getters: {
        user: (state) => state.user,
        initials: (state) => state.user.firstName[0] + state.user.lastName[0]
    },
    mutations: {
        setUser: (state, userData) => {
            state.user.userName = userData.data.name;
            state.user.firstName = userData.data.first_name;
            state.user.lastName = userData.data.last_name;
            state.user.middleName = userData.data.middle_name;
            state.user.title = userData.data.title;
            state.user.email = userData.data.business_email;
            state.user.cellPhone = userData.data.business_phone;
            state.user.internalPhone = userData.data.home_phone;
            state.user.gender = userData.data.gender == 0 ? 'Male' : 'Female';
            state.user.birthData = userData.data.birth_date;
            state.user.officeLocation = userData.data.office_location;
        },
        setImg: (state, blob) => {
            state.user.photo = URL.createObjectURL(blob);
        }
    },
    actions: {
        getMemberData: async (context) => {
            const url = `${opentext.mainurl}/api/v1/members/${opentext.userId}`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
                'Content-Type': 'application/json'
            }});
            context.state.userData = response.data;
            context.commit('setUser', response.data);
        },
        getMemberPhone: async (context) => {
            if(!context.state.userData.data.photo_url) return;
            
            try{
                const url = `${opentext.mainurl}/api/v1/members/${opentext.userId}/photo`;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }, responseType: 'blob' });
                context.commit('setImg', response.data);
            }catch(err){
                console.log(err);
            }
        }
    }
}