<template>
    <v-card
        elevation="2"
    >
        <v-card-text>
            <v-dialog
                @click:outside="onClickOutside"
                v-model="dialog"
                persistent
                max-width="600px"
            >
                <template v-slot:activator = "{ on }">
                    <v-btn v-show="false" v-on="on"></v-btn>
                    <v-container fluid>
                        <v-row>
                            <v-col>
                                <v-text-field
                                    :placeholder="postPlaceholder"
                                    filled
                                    dense
                                    rounded
                                    class="status-field"
                                    readonly
                                    hide-details
                                    @click="openNewStatusWindow"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </template>
                <v-card>
                    <v-card-title>
                        <v-container fluid>
                            <v-row>
                                <v-col></v-col>
                                <v-col class="d-flex justify-center"><span>Create post</span></v-col>
                                <v-col>
                                    <v-btn 
                                      icon
                                      class="float-right"
                                      @click="close"
                                     >
                                      <v-icon>mdi-close</v-icon>
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-container>      
                    </v-card-title>
                    <v-divider class="separator-fix"></v-divider>
                    <v-card-text>
                       <v-container fluid>
                           <v-textarea
                              v-model="post"
                              counter
                              autofocus
                              flat
                              solo
                              rows="5"                        
                              label=""
                              :rules="rules"
                            ></v-textarea>
                       </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-container>
                            <v-row>
                                <v-col cols="6">
                                    <v-btn block text>
                                        <v-icon>mdi-call-made</v-icon>
                                        <span class="ml-1">ATTACH SHORTCUT</span>
                                    </v-btn>
                                </v-col>
                                <v-col cols="6">
                                    <v-btn block text :loading="isSelecting" @click="onAttach">
                                        <v-icon>mdi-cloud-upload-outline</v-icon>
                                        <span class="fix-overflow ml-1">{{attachDocButton}}</span>
                                    </v-btn>
                                    <input
                                      ref="uploader"
                                      class="d-none"
                                      type="file"
                                      @change="onFileChanged"
                                    >
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col>
                                    <v-btn block color="primary" @click="createNewPost">Post</v-btn>
                                </v-col>
                            </v-row>
                        </v-container>
                       
                    </v-card-actions>
                </v-card>
            </v-dialog>
            <v-dialog
              v-model="loadingDialog"
              hide-overlay
              persistent
              width="300"
            >
              <v-card
                color="primary"
                dark
              >
                <v-card-text>
                  Please stand by
                  <v-progress-linear
                    indeterminate
                    color="white"
                    class="mb-0"
                  ></v-progress-linear>
                </v-card-text>
              </v-card>
            </v-dialog>
        </v-card-text>
    </v-card>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'Status',
    computed: {
     ...mapGetters('profile', [
       'user'
     ]),
      postPlaceholder: function(){
          return `What's on your mind, ${this.user.firstName}`
      },
      attachDocButton() {
        return this.selectedFile ? this.selectedFile.name : this.defaultButtonText
      }
    },
    data: () => ({
       loadingDialog: false,
       dialog: false,
       rules: [v => v.length <= 1000 || 'Max 1000 characters'],
       post: '',
       isSelecting: false,
       selectedFile: null,
       defaultButtonText: 'ATTACH DOCUMENT'
    }),
    methods: {
        ...mapActions('newsFeed', [
            'createPost',
            'getPublicPosts'
        ]),
        openNewStatusWindow: function() {
            this.dialog = true;
        },
        close: function(){
            this.post = '';
            this.dialog = false;
            this.selectedFile = null;
        },
        onClickOutside: function(){
            this.close();
        },
        createNewPost: async function(){ 
            if(this.post){
                if(this.selectedFile){
                   await this.createPost({ postStatus: this.post, file: this.selectedFile });
                }else{
                    await this.createPost({ postStatus: this.post });
                }
                this.close();
                this.loadingDialog = true;
                await this.getPublicPosts();
                this.loadingDialog = false;
            }
        },
        onAttach: function(){
            this.isSelecting = true;
            this.$refs.uploader.click()
        },
        onFileChanged: function(e){
            this.selectedFile = e.target.files[0];
            console.log(this.selectedFile);
            this.isSelecting = false;
        }
    }
}
</script>

<style lang="scss">
    //not working with scoped ? find out later reason. 
    .status-field{
        input{
          cursor: pointer;
        }
    }
    .separator-fix{
        margin-top: -10px !important;
    }  
    .fix-overflow{
        width: 150px;
        overflow-x: hidden;
    }
</style>