import axios from 'axios';
import FormData from 'form-data';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';

export default {
    namespaced: true,
    state: () => ({
        publicPostsData: [],
        publicPosts: [],
        likes: []
    }),
    getters: {
        publicPosts: (state) => state.publicPosts
    },
    mutations: {
        
    },
    actions: {
        setPublicPosts: async (context, publicPostsData) => {
            context.state.publicPosts = [];
            for (const postData of publicPostsData) {
                let isLike = false;
                if(context.state.likes.find(x=>x.POSTID == postData.POSTID)){
                    isLike = true;
                }
                const data = JSON.parse(postData.DATA);
                const post = {
                    id: uuidv4(),
                    postId: postData.POSTID,
                    userId: postData.USERID,
                    userName: postData.USERNAME,
                    firstName: postData.FIRSTNAME,
                    lastName: postData.LASTNAME,
                    text: data.text,
                    createdAt: data.created_at,
                    isLike: isLike
                };
                if(data.attachmentID){
                    post.attachmentId = data.attachmentID;
                    const file = await context.dispatch('getFile', post.attachmentId);
                    post.attachment = file;
                }
                context.state.publicPosts.push(post);
            }
        },
        getFile: async (context, fileId) => {
            const url = `${opentext.mainurl}/api/v1/nodes/840753/output?format=json&idx=${fileId}`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            const file = JSON.parse(response.data.data);
            return file;
        },
        getPublicPosts: async (context, date) => {
            try{
                const url = `${opentext.mainurl}/api/v1/nodes/838884/output?format=json`;
                if(date)
                    url+='&date='+moment(date).format('DD/MM/YYY');
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                const data = JSON.parse(response.data.data).result;
                data.pop();
                context.state.publicPostsData = data;
                await context.dispatch('getLikes');
                await context.dispatch('setPublicPosts', data);
                
            }catch(err){
                console.log(err);
            }
        },
        createPost: async (context, post) => {
            const formdata = new FormData();
            formdata.append("ATT_ID", "0");
            formdata.append("ATT_PathSaved:", "");
            formdata.append("status", post.postStatus);
            formdata.append("ATT_Path", "");
            formdata.append("post_audience", "1");
            formdata.append("in_comment_on_obj_id", "0");
            formdata.append("eventType", "status");
            formdata.append("in_reply_to_status_id", "-1");

            if(post.file){
                formdata.append("AddDesktopDoc", post.file, post.file.name);
            }

            const config = {
              method: 'post',
              url: `${opentext.mainurl}/pulse/statuses/update`,
              data : formdata
            };
            try{
                const response = await axios(config)
                console.log(JSON.stringify(response.data));
            }catch(error){
                console.log(error);
            };
        },
        likeOrUnlikePost: async (context, payload) => {
            const url = `${opentext.mainurl}?func=socialobject.LikeOrUnlike&post_id=${payload.postId}&object_id=0&rating_type=${payload.isLike}&feed_type=1`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            console.log(response);
        },
        getLikes: async (context) => {
            const url = `${opentext.mainurl}/api/v1/nodes/840841/output?format=json&userid=${opentext.userId}`;
            const response = await axios.get(url, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            const data = JSON.parse(response.data.data).result;
            data.pop();
            context.state.likes = data;
        }
    }
}