<template>
 <v-container>
    <v-row>
      <v-col cols="2">
        <v-sheet 
          rounded="lg"
          height="85vh"
          justify="center" 
          align="center"
          class="el"
        >
          <v-avatar
            class="mt-2"
            color="primary"
            size="56"
          >
            <span class="initials" v-if="!user.photo">{{initials}}</span>
            <img :src="user.photo" v-if="user.photo">
          </v-avatar>

          <p class="mt-2 fullname">{{user.firstName}} {{user.lastName}}</p>
         

          <v-divider class="my-2"></v-divider>

          <v-list color="transparent">
            <v-list-item
              link
              color="grey lighten-4"
              @click="goTo('dashboard')"
            >
              <v-list-item-content>
                <v-list-item-title>
                        Dashboard
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item
              link
              color="grey lighten-4"
              @click="goTo('newsfeed')"
            >
              <v-list-item-content>
                <v-list-item-title>
                        News Feed
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item
              link
              color="grey lighten-4"
              @click="goTo('profile')"
            >
              <v-list-item-content>
                <v-list-item-title>
                  Profile
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-sheet>
      </v-col>
      <v-col>
          <slot name="content"></slot>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 export default {
   name: 'LayoutDefault',
   data: () => ({ 
   }),
   computed: {
     ...mapGetters('profile', [
       'user',
       'initials'
     ])
   },
   components: {
   },
   methods: {
     ...mapActions('profile', [
       'getMemberData',
       'getMemberPhone'
     ]),
     goTo(routeUrl){
         this.$router.push(routeUrl);
     }
   },
   created: async function(){
     await this.getMemberData();
     await this.getMemberPhone();
   },
 }
</script>

<style lang="scss" scoped>
    .initials {
        color: white
    }
    .fullname {
      font-weight: bold;
    }
    a {
        color: black; /* blue colors for links too */
        text-decoration: inherit; /* no underline */
    }
    .el{
      position: fixed;
      width: 160px;
    }
</style>