<template>
   <v-row>
        <v-col cols="2"></v-col>
        <v-col>
          <v-card>
              <v-card-title>
                  <v-container fluid>
                      <v-row>
                          <v-col>
                              <span class="fullName">{{firstName}} {{lastName}}</span>  
                              <!-- <span class="createdAt">{{createdAt}}</span>                    -->
                          </v-col>
                      </v-row>
                  </v-container>
              </v-card-title>
              <v-card-text class="margin-fix text-post">
                  <v-container fluid>
                       <v-row>
                          <v-col>
                              <span class="text">{{text}}</span>
                          </v-col>
                      </v-row>
                      <v-row>
                          <v-col>
                              <span v-html="attachment"></span>
                          </v-col>
                      </v-row>
                  </v-container>
              </v-card-text>
              <v-divider></v-divider>
              <v-card-actions>
                    <v-container fluid>
                        <v-row>
                            <v-col>
                                <v-btn block text @click="like"><v-icon :class="{like: isLike}">mdi-thumb-up-outline</v-icon><span :class="{like: isLike, 'ml-2': true}">Like</span></v-btn>
                            </v-col>
                            <v-col>
                               <v-btn block text><v-icon>mdi-message-outline</v-icon><span class="ml-2">Comment</span></v-btn>
                            </v-col>
                        </v-row>
                    </v-container>
             </v-card-actions>
          </v-card>
        </v-col>
        <v-col cols="2"></v-col>    
    </v-row>   
</template>

<script>
import moment from 'moment';
import { mapActions } from 'vuex';
export default {
    name: 'PublicPost',
    props: ['postData'],
    computed: {
        firstName: function(){
            return this.postData ? this.postData.firstName: '';
        },
        lastName: function(){
            return this.postData ? this.postData.lastName: '';
        },
        text: function(){
            return this.postData ? this.postData.text: '';
        },
        attachment: function(){
                return this.postData.attachment ? this.postData.attachment.Icon + '&nbsp;<a href="'+this.postData.attachment.URL+'">' 
                + this.postData.attachment.Name + '</a>&nbsp;' : '';
                //+ this.postData.attachment.Menu : ''; TODO: add it later
        },
        createdAt: function(){
            return this.postData ? moment(new Date(this.postData.createdAt)).format('DD/MM/YYYY'): '';
        },
        isLike: function(){
            return this.postData.isLike;
        }
    },
    data: () => ({
    }),
    methods: {
        ...mapActions('newsFeed', [
            'likeOrUnlikePost'
        ]),
        like: async function(){
            await this.likeOrUnlikePost({postId: this.postData.postId, isLike: 1});
            this.postData.isLike = !this.postData.isLike;
        }
    }
}
</script>

<style lang="scss" scoped>
    .fullName{
        font-size: 0.8em;
    }
    .text{
        color: black;
        font-size: 2em;
    }
    .margin-fix{
        margin-top: -25px !important;
    }
    .createdAt{
        display: block;
        font-size: 0.6em;
    }
    .text-post{
        overflow: auto;
        max-height: 150px;
        overflow-x: hidden;
    }
    .like{
        color: blue !important;
    }
</style>