<template>
  <v-app>
    <v-app-bar
      app
      :color="appNavColor"
      flat
    >
    <v-container class="py-0 fill-height">
          <span class="program-name">
            Intranet Portal
          </span>
          <v-btn icon @click="exitApp">
            <v-icon>mdi-exit-to-app</v-icon>
          </v-btn>

          <v-spacer></v-spacer>

          <v-btn icon @click="changeTheme">
            <v-icon>mdi-invert-colors</v-icon>
          </v-btn>
    </v-container>
    </v-app-bar>

    <v-main :class="mainStyles">
      <layout-default>
        <template v-slot:content>
          <router-view></router-view>
        </template>
      </layout-default>
    </v-main>
  </v-app>
</template>

<script>
import LayoutDefault from './layouts/LayoutDefault.vue';
export default {
  name: 'App',
  components:{
    LayoutDefault
  },
  data: () => ({
    appNavColor: 'white',
    mainStyles: 'grey lighten-3'
  }),
  methods:{
    changeTheme: function(){
          this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
          if(this.$vuetify.theme.dark){
            this.appNavColor = 'dark';
            this.mainStyles = '';
          }else{
            this.appNavColor = 'white';
            this.mainStyles = 'grey lighten-3'
          }
    },
    exitApp: function(){
      window.location.href = opentext.mainurl;
    }
  }
};
</script>

<style lang="scss">
  .program-name{
    font-weight: bold;
  }
</style>
