// Polyfills
import 'core-js/stable'
import 'regenerator-runtime/runtime'

import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'
import vuetify from './plugins/vuetify'

Vue.config.productionTip = false

const app = new Vue({
              store,
              router,
              vuetify,
              render: h => h(App)
            }).$mount('#app')
